<template>
  <div class="game-board">
      <div class="tools"></div>
      <div class="main">
        <sidebar @start="start" @over="over" />
        <div class="game">
          <matrixMask />
          <matrix :matrix="matrix" @initMatrixAtFirstClick="initMatrixAtFirstClick" />
        </div>
      </div>
      <div class="info">
      </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import matrix from './matrix'
import sidebar from './sidebar'
import matrixMask from './utils/mask'
//
import { initMatrixSquare, initMatrixMine, initMatrixMineNumber } from '../utils'
import { getLevel } from '../utils/play'
import { level } from '../const'

export default {
  name: 'Board',
  data () {
    return {
      matrixSquare: null,
      customLevel: null,
      matrix: null
    }
  },
  watch: {
    // åˆ‡æ¢éš¾åº¦ä»¥åŽè¦é‡ç½®çŠ¶æ€å’ŒçŸ©é˜µ
    storeLevel: {
      handler (level) {
        // reset state
        this.$store.dispatch('resetState')
        this.initMatrix(level)
      }
    }
  },
  computed: {
    ...mapState(['firstClickSquare']),
    storeLevel () {
      return this.$store.state.level
    }
  },
  mounted () {
    // åˆå§‹åŒ–æ¸¸æˆçŸ©é˜µ
    this.initMatrix(this.storeLevel)
  },
  methods: {
    initMatrix (customLevel) {
      customLevel = this.customLevel = level[getLevel(customLevel)]
      // static temp
      // const isOpenExtends = true
      // åˆå§‹åŒ–çŸ©é˜µ
      let matrixSquare = this.matrixSquare = initMatrixSquare(customLevel)
      // ä¿å­˜çŸ©é˜µé˜µåˆ—å¯ç¿»å¼€çš„æ€»æ•°é‡ï¼ˆæŽ’é™¤äº†åœ°é›·çš„å…¨éƒ¨åŒºå—ï¼‰,è¯¥æ•°é‡å°†ç”¨äºŽåˆ¤å®šè¾“èµ¢
      // this.$store.dispatch('setTotalCount', matrixSquare.row * matrixSquare.col - customLevel.mine)
      this.$store.dispatch('setTotalCount', { matrixSquare: matrixSquare, level: customLevel })
      // åˆå§‹åŒ–åœ°é›·
      // matrixSquare = initMatrixMine(matrixSquare, customLevel, isOpenExtends)
      // åˆå§‹åŒ–æ•°å€¼ï¼ˆåœ°é›·å‘¨å›´çš„æ•°å­—ï¼‰
      // initMatrixMineNumber(matrixSquare.matrix)
      // å¯åŠ¨çŸ©é˜µ
      this.matrix = matrixSquare.matrix
    },
    initMatrixAtFirstClick () {
       // åˆå§‹åŒ–åœ°é›·
      this.matrixSquare = initMatrixMine(this.matrixSquare, this.customLevel, this.firstClickSquare, true)
      // åˆå§‹åŒ–æ•°å€¼ï¼ˆåœ°é›·å‘¨å›´çš„æ•°å­—ï¼‰
      initMatrixMineNumber(this.matrixSquare.matrix)
      // å¯åŠ¨çŸ©é˜µ
      this.matrix = this.matrixSquare.matrix
    },
    start () {
      // reset state
      this.$store.dispatch('resetState')
      // set the play state
      this.$store.dispatch('setPlayState', true)
      // init matrix
      this.initMatrix(this.storeLevel)
    },
    over () {
       // reset state
      this.$store.dispatch('resetState')
      this.$store.dispatch('setPlayState', false)
      this.initMatrix(this.storeLevel)
    }
  },
  components: {
    matrixMask,
    matrix,
    sidebar,
  }
}
</script>
<style lang="scss" scoped>

.game-board {
  display: flex;
  flex-direction: column;
  user-select: none;

  .tools {

  }

  .main {
    display: inherit;
    
    .game {
      position: relative;
      margin-left: 15px;
    }
  }

  .info {

  }
  
}
</style>